// Movie.js
import { useState, useEffect } from "react";
import axios from 'axios'; 
import MovieList from "./MovieList";

export default function Movie(){
    
    const [date, setDate] = useState('20210901');
    const [dataArr,setDataArr] = useState([]);

    function selectDate(e){
        let ymd=e.target.value.split("-");
        setDate(ymd[0]+ymd[1]+ymd[2]);
    }

    useEffect(()=>{
        const axiosEvent = async () => {
            await axios.get('https://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json?key=cad9be1865105b37c7878f3391f806e8&targetDt='+date)
            .then(response=>{
                setDataArr(response.data.boxOfficeResult.dailyBoxOfficeList);
                localStorage.gData = JSON.stringify(response.data.boxOfficeResult.dailyBoxOfficeList);
            })
/* 
            .catch(function (error) {
                // 오류발생시 실행
                // https://wonit.tistory.com/305
            })
 */
        }
        axiosEvent();
    },  [date]);

    //console.log(dataArr)

    return(
        <article>
            <h3>Movie page</h3><br/>
            <input type="date" onChange={selectDate}></input><br/>
            <a href="https://www.kobis.or.kr/kobisopenapi/homepg/apiservice/searchServiceInfo.do" target="_blank" rel="noreferrer" className="instruction">설명서</a>

{/* 
            <table>
                <thead>
                    <tr>
                        <th>응답 필드</th>
                        <th>설명</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>audiAcc</td>
                        <td>누적관객수</td>
                    </tr>
                    <tr>
                        <td>audiChange</td>
                        <td>전일 대비 관객수 증감 비율</td>
                    </tr>
                    <tr>
                        <td>audiCnt</td>
                        <td>해당일의 관객수</td>
                    </tr>
                    <tr>
                        <td>audiInten</td>
                        <td>전일 대비 관객수 증감분</td>
                    </tr>
                    <tr>
                        <td>movieCd</td>
                        <td>영화의 대표코드</td>
                    </tr>
                    <tr>
                        <td>movieNm</td>
                        <td>영화명(국문)</td>
                    </tr>
                    <tr>
                        <td>openDt</td>
                        <td>영화의 개봉일</td>
                    </tr>
                    <tr>
                        <td>rank</td>
                        <td>해당일자의 박스오피스 순위</td>
                    </tr>
                    <tr>
                        <td>rankInten</td>
                        <td>전일대비 순위의 증감분</td>
                    </tr>
                    <tr>
                        <td>rankOldAndNew</td>
                        <td>
                            랭킹에 신규진입여부 <br/> 
                            “OLD” : 기존 , “NEW” : 신규
                        </td>
                    </tr>
                    <tr>
                        <td>rnum</td>
                        <td>순번</td>
                    </tr>
                    <tr>
                        <td>salesAcc</td>
                        <td>누적매출액</td>
                    </tr>
                    <tr>
                        <td>salesAmt</td>
                        <td>해당일의 매출액</td>
                    </tr>
                    <tr>
                        <td>salesChange</td>
                        <td>전일 대비 매출액 증감 비율</td>
                    </tr>
                    <tr>
                        <td>salesInten</td>
                        <td>전일 대비 매출액 증감분</td>
                    </tr>
                    <tr>
                        <td>salesShare</td>
                        <td>
                            해당일자 상영작의 <br/> 
                            매출총액 대비 해당 영화의 매출비율
                        </td>
                    </tr>
                    <tr>
                        <td>scrnCnt</td>
                        <td>해당일자에 상영한 스크린수</td>
                    </tr>
                    <tr>
                        <td>showCnt</td>
                        <td>해당일자에 상영된 횟수</td>
                    </tr>
                </tbody>
            </table>
 */}

            <MovieList movies={dataArr} date={date}/>
        </article>
    );
}
