// MovieList.js

export default function MovieList({ movies, date }){
    return( 
        <>
            <h3>MovieList Page ({date})</h3>
            <table className="movieListTable">
                <thead>
                    <tr>
                        <th>당일 순위</th>
                        <th>영화제목</th>
                        <th>당일 관객수</th>
                        <th>당일 매출액</th>
                    </tr>
                </thead>
                <tbody>
                    {movies.map(movies => {
                        return(
                            <tr key={movies.movieCd}>
                                <td>{movies.rank}</td>
                                <td>{movies.movieNm}</td>
                                <td>{movies.audiCnt}</td>
                                <td>{movies.salesAmt}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </>
    );
}
